body {
  -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  scrollbar-width: none;
  /* Firefox */
}

body::-webkit-scrollbar {
  display: none;
  /* Safari and Chrome */
}

body {
  background-color: black;
}

header {
  background-color: black;
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
  position: sticky;
  top: 0px;
}

.headerp {
  font-size: 25px;
}

.headCombo {
  cursor: pointer;
  font-size: 17px;
}

.headCombo:hover {
  color: gold;
}

.headChat {
  display: none;
}

.headChat:hover {
  color: gold;
}

.headBMenu {
  display: none;
}

.introduction {
  background-color: rgb(123, 19, 221);
  width: 95%;
  border-radius: 10px;
}

.text1 {
  font-size: 35px;
  font-family: 'Times New Roman', Times, serif;
  font-style: italic;
}

.letstalk {
  background-color: gold;
  color: black;
  cursor: pointer;
  border: none;
  font-size: 17px;
  padding-top: 7px;
  padding-bottom: 7px;
}

.letstalk:hover {
  background-color: black;
  color: white;
}

.introimg img {
  width: 330px;
}

.pop-up {
  background-color: black;
  color: white;
  width: 330px;
  /* display: flex;
  justify-content: space-evenly;
  align-items: center; */
  border-radius: 10px;
  position: fixed;
  top: 50vh;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.1);
  transition: transform 0.4s;
  visibility: hidden;
}

.pophead {
  width: 100px;
  position: relative;
  top: -50px;
  border-radius: 50%;
  margin-bottom: -40px;
}

.contact {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  border: 0.5px solid gold;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pop-ups {
  visibility: visible;
  transform: translate(-50%, -50%) scale(1);
}

.blurback {
  filter: blur(1.5px);
}

.popbtn {
  width: 90%;
  background-color: goldenrod;
  color: black;
  font-size: 20px;
  border-radius: 10px;
}

.popbtn:hover {
  background-color: gold;
}

.skillComp, .servComp {
  width: 95%;
  height: 95%;
  max-width: 500px;
  max-height: 600px;
  background-color: black;
  border: 0.1px solid gold;
  border-radius: 15px;
  position: fixed;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%) scale(0.1);
  transition: transform 0.4s;
  visibility: hidden;
}

.skillComps, .servComps {
  visibility: visible;
  transform: translate(-50%, -50%) scale(1);
}

.skillComp h4, .servComp h4 {
  background-color: goldenrod;
  border-radius: 10px;
  width: 150px;
  height: 38px;
  box-shadow: 1.5px 1.5px gold;
}

.skillComp p, .servComp p {
  color: white;
  border: 0.5px solid goldenrod;
  border-radius: 5px;
  padding: 7px;
  padding-left: 20px;
  margin: 7px;
  margin-left: 20px;
  margin-right: 20px;
}

.skillComp img, .servComp img {
  width: 35px;
  background-color: gold;
  border-radius: 50%;
  border: 1px solid gold;
  position: absolute;
  right: 5px;
  top: 5px;
  cursor: pointer;
}

.awh {
  /* background-color: goldenrod; */
  color: goldenrod;
  border: 1px solid goldenrod;
  border-radius: 5px;
  padding: 9px;
  max-width: 300px;
}

.proj1,
.proj2,
.proj3 {
  /* max-width: 375px; */
  border: 1px solid goldenrod;
  border-radius: 2px;

  padding: 20px;
}

.image {
  width: 100%;
  max-width: 350px;
  aspect-ratio: 16/9;
}

.proj p {
  color: white;
  width: 100%;
  margin-top: 12px;
  font-weight: 500;
}

.visitSiteA {
  border: none;
  outline: none;
  border-radius: 5px;
  margin-top: 5px;
  color: black;
  background-color: goldenrod;
  width: 80%;
  min-width: 85.18px;
  height: 35px;
  font-weight: bold;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.githubA {
  border: none;
  outline: none;
  border-radius: 5px;
  margin-top: 5px;
  margin-left: 5px;
  color: black;
  background-color: goldenrod;
  width: 43px;
  height: 35px;
  font-weight: bold;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.proj a:hover {
  background-color: darkgoldenrod;
  color: black;
}

.skillCard {
  display: grid;
  gap: 1rem;
  grid-auto-flow: column;
  grid-auto-columns: fit-content;

  overflow-x: auto;
  overscroll-behavior-inline: contain;
}

.skillCard > div {
  /* border: 1px solid goldenrod; */
  border-radius: 10px;
  font-weight: bold;
  font-style: italic;
  margin: 2rem 0px;
  text-align: left;
  padding-inline: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cardScrollBar::-webkit-scrollbar {
  height: 5px;
}

.cardScrollBar::-webkit-scrollbar-track {
  background-color: darkslategrey;
  border-radius: 5px;
  cursor: pointer;
}

.cardScrollBar::-webkit-scrollbar-thumb {
  background-color: darkgoldenrod;
  border-radius: 5px;
}

.myCardCon {
  display: grid;
  gap: 1rem;
  grid-auto-flow: column;
  grid-auto-columns: 21%;

  overflow-x: auto;
  overscroll-behavior-inline: contain;
}

.snapCard {
  scroll-snap-type: inline mandatory;
  scroll-padding-inline: 1rem;
}

.snapCard > * {
  scroll-snap-align: start;
}

footer {
  height: 100px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0px !important;
}

/* Media Quries */

@media screen and (min-width: 500px) {
  /* header {
      display: flex;
      justify-content: space-between;
      padding-left: 20px;
      padding-right: 20px;
  } */


  .headChat {
      display: block;
      cursor: pointer;
      font-size: 17px;
  }

  .pop-up {
      width: 360px;
  }
}

@media screen and (min-width: 600px) {
  .introFlex {
      display: flex;
      padding-top: 50px;
      justify-content: center;
      max-width: 1000px;
  }
}

@media screen and (min-width: 700px) {
  .headBMenu {
    display: flex;
  }

  .menuBar {
    display: none;
  }
  .menuItems {
    visibility: hidden;
  }
}

@media screen and (max-width: 700px) {
  .menuBar img {
    width: 35px;
    border: 0.1px solid gold;
    background-color: gold;
    border-radius: 50%;
    cursor: pointer;
  }

  .menuItems {
    visibility: hidden;
    background-color: goldenrod;
    width: 0px;
    height: 0%;
    position: fixed;
    top: 68px;
    right: 150px;
    transition: width 0.4s, height 0.4s;
  }

  .menuItemss {
    right: 0px;
    visibility: visible;
    width: 200px;
    height: 100%;
  }

  .menuItems button {
    display: block;
    background-color: black;
    color: white;
    border: none;
    border-radius: 5px;
    width: 130px;
    margin: 9px;
    padding: 5px;
    box-shadow: 3px 3px rgb(61, 48, 48);
  }
}

@media screen and (max-width: 500px) {
  .letstalk {
    padding-bottom: 6px;
  }
}

@media screen and (max-width: 400px) {
  .myCardCon {
    grid-auto-columns: 80%;
  }
}

@media screen and (min-width: 400px) {
  .myCardCon {
    grid-auto-columns: 60%;
  }
}

@media screen and (min-width: 500px) {
  .myCardCon {
    grid-auto-columns: 42%;
  }
}

@media screen and (min-width: 700px) {
  .myCardCon {
    grid-auto-columns: 36%;
  }
}

@media screen and (min-width: 900px) {
  .myCardCon {
    grid-auto-columns: 29%;
  }
}

@media screen and (min-width: 1000px) {
  .myCardCon {
    grid-auto-columns: 23%;
  }
}

@media screen and (min-width: 1100px) {
  .myCardCon {
    grid-auto-columns: 21%;
  }
}